<template>
  <div class="playListDetail">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="songList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <div v-if="songList!==''">
      <div :class="['header',playListInfo.titleImageUrl == null&&isHidden?'header-gedan':'']">
      <div :class="[playListInfo.titleImageUrl !== null?'left':'left-gedan']">
        <i @click="backIndex" class="iconfont icon-arrowLeft"></i>
        <img src="../../assets/image/default.png" alt="" />
        <span >{{playListInfo.titleImageUrl !== null?'官方动态歌单':'歌单'}}</span>
      </div>
      <div class="right">
        <span class="iconfont icon-search"></span>
        <span class="iconfont icon-liebiao-dian"></span>
      </div>
    </div>
    <div v-if="playListInfo.titleImageUrl !== null" class="center">
      <img :class="['bg',isHidden?'bg2':'']" :src="playListInfo.backgroundCoverUrl+'?param=500y500'" alt="" />
      <img :class="['title',isHidden?'hidden':'']" :src="playListInfo.titleImageUrl+'?param=500y500'" alt="" />
      <p :class="['text1',isHidden?'hidden':'']">{{ playListInfo.updateFrequency }}</p>
      <p :class="['text2',isHidden?'hidden':'']">{{ playListInfo.description }}</p>
    </div>
    <div v-if="playListInfo.titleImageUrl === null" class="center2">
      <img class="coverImg" :src="playListInfo.coverImgUrl+'?param=500y500'" alt="" />
      <div class="info">
        
      <p id="name" class="name">{{ playListInfo.name }}</p>
      <p class="user"><img class="avatar" :src="playListInfo.avatarUrl+'?param=100y100'" alt="" />&emsp;{{ playListInfo.nickname }}</p>
      <p class="description">{{ playListInfo.description }}</p>
      </div>
    </div>
      <div :class="[playListInfo.titleImageUrl !== null?'line':'line-gedan',isHidden?'hidden':'']">
        <div class="line-content">
          <span class="iconfont icon-dingyue"></span
        ><span class="count">{{ playListInfo.subscribedCount }}</span>
        <span class="">|</span>
        <span class="iconfont icon-pinglun"></span
        ><span class="count">{{ playListInfo.commentCount }}</span>
        <span class="">|</span>
        <span class="iconfont icon-fenxiang"></span
        ><span class="count">{{ playListInfo.commentCount }}</span>
        </div>
      </div>
    <div :class="[playListInfo.titleImageUrl !== null?'list-header':'list-header-gedan',isHidden?'list-header-fixed':'']">
      <div class="playBtn">
        <div class="left">
          <div class="left-icon">
            <span @click="playAll" class="iconfont icon-caret_right"></span>
          </div>
          <span>播放全部</span><span class="list-count">({{songList.length}})</span>
        </div>
        <div class="right">
          <span class="iconfont icon-download"></span>
          <span class="iconfont icon-icon-test"></span>
        </div>
      </div>
    </div>
    <div ref="pronbit" :class="[playListInfo.titleImageUrl !== null?'main':'main-gedan']" >
      <div v-for="(item, index) in songList" :key="index" class="song">
        <div class="item">
          <div class="left" @click="playThis(item.id,index)">
            <div class="text">
              <span>{{ index + 1 }}</span>
            </div>
            <div class="track">
              <p class="name">{{ item.name }}</p>
              <p class="singer">{{artistsList[index]}}</p>
            </div>
          </div>
          <div class="icon">
            <span class="iconfont icon-bofang"></span>
            <span class="iconfont icon-liebiao-dian"></span>
          </div>
        </div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import { getPlayListDetail } from "../../NetRequest/more";
import { toStringNum } from "../../common/common";
import { getMusicDetail } from "../../NetRequest/play";
import { findItem } from '../../common/common'
export default {
  data() {
    return {
      id: "",
      playListInfo: {
        coverImgUrl: "",
        backgroundCoverUrl:'',
        avatarUrl:'',
        name:'',
        nickname:'',
        titleImageUrl: "",
        updateFrequency: "",
        description: "",
        subscribedCount: "",
        commentCount: "",
        shareCount: ""
      },
      trackIds: [],
      artistsList: [],
      songList: [],
      hasClcikMusicId:'',
      isHidden:false,
      screenHeight:''
    };
  },
  mounted() {
    // console.log(this.$route.query.id);
    this.id = this.$route.query.id;
      setTimeout(()=>{
        this.getDetail();
      },1500)
    window.addEventListener('scroll',this.handleScrollx,true)
  },
  methods: {

    handleScrollx() {
  // console.log('滚动高度',window.pageYOffset)
  // console.log('距离顶部高度',this.$refs.pronbit.getBoundingClientRect().top)
  let topOffset = this.$refs.pronbit.getBoundingClientRect().top
  if(this.playListInfo.titleImageUrl !== null){
    topOffset <= 130&&topOffset != 40&&topOffset != 20? this.isHidden = true : this.isHidden = false
  }else{
    topOffset <= 170&&topOffset != 40&&topOffset != 20? this.isHidden = true : this.isHidden = false
  }
 },
    async getDetail() {
      const res = await getPlayListDetail(this.id);
      // console.log(res);
      const result = res.data.playlist;
      this.playListInfo.backgroundCoverUrl = result.backgroundCoverUrl;
      this.playListInfo.coverImgUrl = result.coverImgUrl;
      this.playListInfo.avatarUrl = result.creator.avatarUrl;
      this.playListInfo.name = result.name;
      this.playListInfo.nickname = result.creator.nickname;
      this.playListInfo.name = result.name;
      this.playListInfo.titleImageUrl = result.titleImageUrl;
      this.playListInfo.updateFrequency = result.updateFrequency;
      this.playListInfo.description = result.description;
      this.playListInfo.subscribedCount = toStringNum(result.subscribedCount);
      this.playListInfo.commentCount = toStringNum(result.commentCount);
      this.playListInfo.shareCount = toStringNum(result.shareCount);
      for (const item of res.data.playlist.trackIds) {
        this.trackIds.push(item.id);
      }
      this.trackIds = this.trackIds.join(",");
      this.myGetMusicDetail();
      //   console.log(this.trackIds[0]);
    },
    /* 获取歌曲详情 */
    async myGetMusicDetail() {
      const res = await getMusicDetail(this.trackIds);
    //   console.log(res);
      for (const item of res.data.songs) {
        this.songList.push({
          id: item.id,
          name: item.name,
          artists: item.ar[0].name,
          picUrl: item.al.picUrl,
        });
      }
      for(let i = 0;i < this.songList.length;i++){
          let result = []
          for (const item of res.data.songs[i].ar) {
            result.push(item.name);
        this.artistsList[i] = result.join(' / ')
      }}
    //   console.log(this.artistsList);
    },
    /* 返回首页 */
    backIndex() {
      this.$router.go(-1);
    },
    /* 点击播放歌曲 */
    playThis(musicId,i){
      // console.log(musicId);
      this.$store.state.playShow = true
      const index = findItem(this.$store.state.playList,'id',musicId)
      if(index != -1){//如果播放列表有这首歌
        if(this.hasClcikMusicId == musicId){
          //如果被点击这首歌已经被点击过且正在播放
          return
        }
        this.$bus.$emit('receiveMusicId',musicId,index)
      } else{//播放列表没有这首歌
        const newIndex = this.$store.state.playList.length
        this.$store.state.playList.push(this.songList[i])
        // console.log(this.$store.state.playList);
        this.$bus.$emit('receiveMusicId',musicId,newIndex)
        this.hasClcikMusicId = musicId
      }
    },
    /* 播放全部 */
    playAll(){
      this.$store.state.playList=this.songList
      this.$toast('已添加到播放列表');
      setTimeout(()=>{
        this.$store.state.playShow = true
        this.$bus.$emit('receiveMusicId',this.songList[0].id,0)
        },1000)
      
      localStorage.removeItem('newSong')
      localStorage.setItem("newSong", JSON.stringify(this.songList));

    }
  },
  beforeDestroy() {
    window.removeEventListener('scroll',this.handleScrollx,true)
  },
};
</script>

<style scoped>
