import { request } from "./request";

//歌单
export function getPlayList(cat,limit,offset){
    return request ({
        url: '/top/playlist',
        params: {
            cat,
            limit,
            offset
        }
    })
}
//歌单详情
export function getPlayListDetail(id){
    return request ({
        url: '/playlist/detail',
        params: {
            id
        }
    })
}